import React, { useEffect, useState } from "react"
import * as styles from "./header.module.css"
import {Link} from '../index'

const MENU ={
  "/about": "ABOUT",
  "/works": "WORKS",
  "/blog": "BLOG"
}

const Header = () =>  {
  return (
    <header className="flex flex-nowrap justify-between w-11/12 mx-auto mb-4">
      <Link to="/" appearance="default">
        <h1 className={styles.logo}>CT</h1>
      </Link>
      <nav className={styles.nav}>
        {
          Object.keys(MENU).map((route) => {
            return(
              <Link key={route} to={route}>
                {MENU[route]}
              </Link>
            )
          })
        }
      </nav>
    </header>
  )
}

export default  Header