import React from 'react'
import { Link } from "gatsby"
import * as styles from './link.module.css'
import cn from 'classnames'

export default function CustomLink({appearance, to, children, external}) {
  return external ?
    <a  target="_blank" className={cn(
      styles.base,
      appearance === "default" ? styles.black : undefined
    )} href={to}>{children}</a>
    :
    <Link to={to} className={cn(
      styles.base,
      appearance === "default" ? styles.black : undefined
    )}>{children}</Link>
}

CustomLink.defaultProps = {
  external: false
}